<template>
  <div class="settings-wrapper">
    <div class="settings">
      <!-- Radios -->
      <div class="field radios">
        <div class="radio">
          <label class="container">
            Tage
            <!-- TODO v-model записать в store -->
            <input
              type="radio"
              name="radio"
              id="tage"
              value="tage"
              @click="saveTermin({
                termin:'tage',
                range: 30,
              })"
            >
            <span class="checkmark"></span>
          </label>
        </div>

        <div class="radio">
          <label class="container">
            Monate
            <input
              name="radio"
              type="radio"
              id="monate"
              value="monate"
              @click="saveTermin({
                termin:'monate',
                range: 1,
              })"
            >
            <span class="checkmark"></span>
          </label>
        </div>
        <div class="radio">
          <label class="container">
            Jahre
            <input
              name="radio"
              type="radio"
              id="jahre"
              value="jahre"
              @click="saveTermin({
                termin:'jahre',
                range: 1,
              })"
            >
            <span class="checkmark"></span>
          </label>
        </div>
      </div>
      <!-- /.radios -->
      <div class="field range" v-show="getTermin !== ''">
        <div class="range-value" v-if="range !== 0">{{ range }}</div>
        <div class="range-value" v-else>{{ getVal.min }}</div>
        <input
          type="range"
          :min="getVal.min"
          :max="getVal.max"
          @mouseup="saveRange(range)"
          v-model="range"
        >
      </div>
      <!-- /. Range -->
      <app-checkbox v-show="getTermin==='jahre'"></app-checkbox>
    </div>
    <!-- settings -->
  </div>
</template>
<script>
import Mix from "../mixins/forms.js";

export default {
  methods: {
    saveTermin(termin) {
      this.$store.commit("changeTermin", termin);
    },
    saveRange(val) {
      this.$store.commit("changeRange", val);
    }
  },
  mixins: [Mix]
};
</script>

<style>
</style>




