export default {
  data() {
    return {
      kapital: "",
      satz: "",
      percent: "",
      kapitalNew: "",
      msg: null,
      flag: false,
      showMsg: false,
      min: "",
      max: "",
      range: 0,
      termin: "",
      pattern: /^[\d]{1,6}\.?[\d]{0,2}$/,
      showPers: false,
      showKap: false,
      result: ""
    };
  },
  computed: {
    getVal() {
      if (this.termin === "jahre") {
        this.min = 1;
        this.max = 30;
      }
      if (this.termin === "monate") {
        this.min = 1;
        this.max = 12;
      }
      if (this.termin === "tage") {
        this.min = 30;
        this.max = 360;
      }
      this.range = this.min;
      return {
        min: this.min,
        max: this.max
      };
    },
    showSettings() {
      return this.$store.state.settingsVisibility;
    },
    getTermin() {
      this.termin = this.$store.state.termin;
      return this.termin;
    }
  },
  methods: {
    /**
     *  функция валидации даных для функций setKap и setpercent
     * e -  событие input
     * str - значение ключа this.kap или this.persent (string)
     */
    validate(str) {
      let test = this.pattern.test(str);
      if (!test && str !== "") {
        str = str.slice(0, -1);
        this.msg = "nur Zhalen";
        if (str.charAt(str.length - 4)) {
          // Если после точки ставить больше 2 знаков - несоответствие шаблону и соответств. message
          this.msg = "2 Zeichen nach der Koma!";
        }
        if (str.length >= 8) {
          this.msg = "nicht mehr als 8 Ziefern";
        }

        this.flag = true;
      }

      return str;
    }, // validate
  },
  created() {
    this.$store.state.termin = "";
    this.$store.state.settingsVisibility = false;
    this.$store.state.range = "";
  }
};



