<template>
  <label class="field checkbox">
    percent only
    <input type="checkbox" checked="checked" @click="sum = !sum">
    <span class="checkmark"></span>
  </label>
</template>

<script>
export default {
  data() {
    return {
      sum: false
    };
  }
};
</script>



