<template>
  <div class="component-wrapper">
    <h1>Zinsen berechnen</h1>

    <div class="input-text-wrapper">
      <div class="field text-input">
        <label class="input-title" for="kapital">kapital:</label>
        <input type="text" @input="setKap" @focus="erseMsg" v-model="kapital">
        <div class="tooltip" v-if="showKap">{{msg}}</div>
      </div>

      <!-- this.persent -->
      <div class="field text-input">
        <label class="input-title" for="prozentsatz">prozentsatz:</label>
        <input type="text" @input="setPercent" @focus="erseMsg" v-model="percent">
        <div class="tooltip" v-if="showPers">{{msg}}</div>
      </div>
      <div class="common-msg" v-if="showMsg">{{msg}}</div>
    </div>
    <app-settings v-show="showSettings"></app-settings>

    <button class="btn" @click="getResult">berechnen</button>

    <h2>Ergebnis(Teil)</h2>
    <div class="display">
      <div class="result">{{result}}</div>
    </div>
  </div>
</template>
<script>
import Mix from "../mixins/forms.js";

export default {
  data() {
    return {
      sum: false
    };
  },
  mixins: [Mix],
  created() {
    this.$store.commit("showSettingsBtn");
  },
  methods: {
    setPercent(e) {
      this.showKap = false;
      this.percent = this.validate(this.percent).trim();
      if (this.flag) {
        this.flag = false;
        this.showPers = true;
      } else {
        this.showPers = false;
      }
    },

    setKap(e) {
      this.showPers = false;
      this.kapital = this.validate(this.kapital).trim();
      if (this.flag) {
        this.flag = false;
        this.showKap = true;
      } else {
        this.showKap = false;
      }
    },
    erseMsg() {
      this.showMsg = false;
      this.showPers = false;
      this.showKap = false;
    },

    setTime(e) {},

    getResult() {
      if (this.kapital === "" || this.percent === "") {
        this.msg = "заполните все поля!";
        this.showMsg = true;
        return;
      }
      if (this.getTermin === "") {
        this.result =
          (parseFloat(this.kapital) * parseFloat(this.percent)) / 100;
        this.result = parseFloat(this.result.toFixed(2));
      }
      if (this.getTermin === "tage") {
        this.result =
          (parseInt(this.kapital) *
            parseInt(this.percent) *
            parseInt(this.$store.state.range)) /
          (100 * 360);
        this.result = parseFloat(this.result.toFixed(2));
      }
      if (this.getTermin === "monate") {
        this.result =
          (parseFloat(this.kapital) *
            parseFloat(this.percent) *
            parseInt(this.$store.state.range)) /
          (100 * 12);
        this.result = parseFloat(this.result.toFixed(2));
      }
      if (this.getTermin === "jahre") {
        let kapital = 1 + parseFloat(this.percent) / 100;
        kapital = Math.pow(kapital, this.$store.state.range);
        this.kapitalNew = kapital * this.kapital;
        if (this.sum) {
          this.result = parseFloat(this.kapitalNew).toFixed(2);
        } else {
          this.result = parseFloat(this.kapitalNew - this.kapital).toFixed(2);
        }
      }
    }
  }
};
</script>
<style lang="scss" scoped>
h1,
h2 {
  text-align: center;
  margin-top: 20px;
  margin-bottom: 20px;
}
input[type="text"] {
  // width: 40px;
  // border: 1px solid black;
  // outline: none;
  // padding: 5px;
}
.field {
  // display: flex;
  // justify-content: space-between;
  // width: 150px;
  // margin: 20px auto;
  // position: relative;
}
.btn {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}
.range-value {
  // position: absolute;
  // top: -50px;
  // right: -80px;
  // width: 40px;
  // height: 40px;
  // line-height: 40px;
  // border-radius: 50%;
  // background-color: #cee;
  // text-align: center;
  // font-size: 18px;
  // font-weight: 800;
  // text-align: center;
}
</style>



