<template>
  <div class="component-wrapper">
    <h1>Prozentsatz berechnen</h1>
    <div class="input-text-wrapper">
      <div class="field text-input">
        <label>Kapital:</label>
        <input type="text" @focus="erseMsg" @input="setKap" v-model="kapital">
        <div class="tooltip" v-if="showKap">{{ msg }}</div>
      </div>
      <div class="field text-input">
        <label>{{ dynTitle }}:</label>
        <input type="text" @focus="erseMsg" @input="setPercent" v-model="percent">
        <div class="tooltip" v-if="showPers">{{ msg }}</div>
      </div>
      <div class="common-msg" v-if="showMsg">{{ msg }}</div>
    </div>
    <app-settings v-show="showSettings"></app-settings>
    <button class="btn" @click="getResult">berechnen</button>
    <div class="result">
      <h2>Ergebnis(prozentsatz)</h2>
      <div class="display">{{ result }}</div>
    </div>
  </div>
</template>
<script>
import Mix from "../mixins/forms.js";
export default {
  data() {
    return {
      title: {
        first: "Kapital",
        second: "Prozentteil"
      }
    };
  },
  created() {
    this.$store.commit("showSettingsBtn");
  },
  computed: {
    dynTitle() {
      if (this.getTermin === "jahre") {
        this.title.second = "Neues Kapital";
      } else {
        this.title.second = "Prozentteil";
      }
      return this.title.second;
    }
  },
  mixins: [Mix],
  methods: {
    setPercent(e) {
      this.showKap = false;
      this.percent = this.validate(this.percent).trim();
      if (this.flag) {
        this.flag = false;
        this.showPers = true;
      } else {
        this.showPers = false;
      }
    },

    setKap(e) {
      this.showPers = false;
      this.kapital = this.validate(this.kapital).trim();
      if (this.flag) {
        this.flag = false;
        this.showKap = true;
      } else {
        this.showKap = false;
      }
    },
    erseMsg() {
      this.showMsg = false;
      this.showSatz = false;
      this.showKap = false;
    },
    setTime() {},
    getResult() {
      if (this.kapital == "" || this.percent == "") {
        this.msg = "Es gibt noch leere felder";
        this.showMsg = true;
        return;
      }
      if (this.getTermin === "") {
        this.result =
          (parseFloat(this.percent) * 100) / parseFloat(this.kapital);
        this.result = parseFloat(this.result.toFixed(2));
      }

      if (this.getTermin === "tage") {
        this.result =
          (parseInt(this.percent) * 36000) /
          (parseInt(this.kapital) * parseInt(this.$store.state.range));
        this.result = parseFloat(this.result.toFixed(2));
      }

      if (this.getTermin === "monate") {
        console.log(this.getTermin);

        this.result =
          (parseInt(this.percent) * 1200) /
          (parseInt(this.kapital) * parseInt(this.$store.state.range));
        this.result = parseFloat(this.result.toFixed(2));
        console.log(this.$store.state.range);
      }

      if (this.$store.state.termin === "jahre") {
        /**
         * TODO нужно * prozentteil * поменять на neues Kapital
         * (прцентная часть становиться больше первоночально вложенноо капитала)
         *  получается что часть становиться новым капиталом
         * в формуле его нужно делить на * kapital *
         **/

        let kap =
          (parseInt(this.percent) + parseInt(this.kapital)) / this.kapital;
        let power = (kap ** (1 / parseInt(this.$store.state.range)) - 1) * 100;
        // this.percent = parseInt(this.percent);
        this.result = parseFloat(power.toFixed(2));
      }
    }
  }
};
</script>
<style lang="scss" scoped>
h1,
h2 {
  text-align: center;
  /*margin-top: 20px;*/
  /*margin-bottom: 20px;*/
}
// input[type="text"] {
//   width: 40px;
//   border: 1px solid black;
//   outline: none;
//   padding: 5px;
// }
// .field {
//   display: flex;
//   justify-content: space-between;
//   width: 150px;
//   margin: 20px auto;
// }
.btn {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 100px;
}
</style>



