</script>

<style lang="scss">
/** Main Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #ee4;
  font-family: "Open Sans";
  height: 100%;
  overflow: hidden;
}
h {
  color: rgba(#000, 0.75);
  margin-bottom: 40px;
  font-size: 2.5rem;
}
/** WRAPPERS*/
.wrapper {
  // position: absolute;
  // TODO разобраться с размером врапера
  // top: 50%;
  // left: 50%;
  // transform: translate(-50%, -50%);
  min-height: 95vh;
  background-color: #fff;
  padding: 20px;
}
.component-wrapper {
  /*border: 1px solid gray;*/
  position: relative;
  /*top: 50%;*/
  /*left: 50%;*/
  /*<!--transform: translate(-50%, -50%);-->*/
  width: 100%;
  height: 100vh;
  padding-top: 20px;
}
.field {
  display: flex;
  justify-content: center;
  /*max-width: 150px;*/
  position: relative;

  .tooltip {
    position: absolute;
    right: 50%;
    bottom: 0;
    transform: translate(50%, 100%);
    color: red;
    // top: 0px;
  }
}
/** MENU */
.menu {
  margin: 0 auto;
  max-width: 800px;
  z-index: 888;
  position: relative;
  border-bottom: 1px solid #ccc;
  img {
    cursor: pointer;
  }
}
/** INPUT TYPE=TEXT */
.input-text-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  position: relative;
  .common-msg {
    position: absolute;
    right: 50%;
    top: 100%;
    transform: translate(50%, 10px);
    color: red;
  }
}
.text-input {
  flex-wrap: wrap;
  flex-direction: column;
  align-items: flex-end;
  margin-left: 20px;
  margin-right: 20px;
  label {
    width: 245px;
  }
}
.input-title {
  font-weight: bold;
  font-size: 12px;
}
input {
  text-align: center;
  /*border: 3px solid darkred;*/
  border: none;
  background-color: rgba(#ddd, 0.6);
  outline: none;
  padding: 5px;
}
input[type="text"] {
  width: 245px;
  height: 95px;
  font-size: 38px;
  padding: 10px;
  border-radius: 2px;
}
/* settings */
.settings-btn {
  position: absolute;
  right: 0;
  top: 0;
  z-index: 9999;
  img {
    width: 30px;
    cursor: pointer;
  }
}
.settings {
  position: absolute;
  width: 250px;
  display: flex;
  flex-direction: column;
  height: 400px;
  background-color: rgba(#ccc, 0.9);
  /*border-radius: 5px;*/
  right: 0;
  top: 0;
  z-index: 999;
}
/** INPUT TYPE=RADIO */
.radios {
  display: flex;

  justify-content: center;
  margin-top: 30px;

  .radio {
    position: relative;
    margin-left: 30px;
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    width: 40px;
    height: 60px;
    font-size: 11px;
    font-weight: bold;

    label {
      position: relative;
      width: 100%;
    }

    .checkmark {
      margin: 0 auto;
      margin-top: 3px;
      width: 15px;
      height: 15px;
      background-color: #eee;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border-radius: 50%;
      cursor: pointer;

      &:hover {
        background-color: #ccc;
      }

      &:after {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: none;
        width: 5px;
        height: 5px;
        background-color: darkred;
        border-radius: 50%;
      }
    }

    input {
      width: 0;
      height: 0;
      opacity: 0;
      position: absolute;
      cursor: pointer;

      &:checked ~ .checkmark {
        background-color: #faeec5;

        &:after {
          display: block;
        }
      }
    }
  }
}
/** INPUT TYPE=checkbox */
.checkbox {
  display: block;
  position: relative;
  padding-right: 40px;
  text-align: right;
  user-select: none;
  cursor: pointer;
  input {
    width: 0;
    height: 0;
    opacity: 0;
    position: absolute;
    cursor: pointer;
    &:checked ~ .checkmark {
      background-color: #faeec5;
      &:after {
        display: block;
      }
    }
  }
  .checkmark {
    position: absolute;
    top: 0;
    right: 0;
    height: 25px;
    width: 25px;
    background-color: #eee;
    cursor: pointer;
    &:hover {
      background-color: #ccc;
    }
  }
  /* Create the checkmark/indicator (hidden when not checked) */
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }

  /* Show the checkmark when checked */
  .checkbox input:checked ~ .checkmark:after {
    display: block;
  }

  /* Style the checkmark/indicator */
  .checkmark:after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid darkred;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }
}

/** range */
.range {
  margin-top: 120px;
  position: relative;

  input {
    width: 100%;
    padding: 0;
    height: 5px;
    border-radius: 5px;
    -webkit-appearance: none;
    -moz-appearance: none;
    background-color: white;
    box-shadow: inset 0 3px 18px rgba(darkred, 0.7);
    outline: none;
    margin: 20px;
    &::-webkit-slider-thumb {
      cursor: pointer;
      -webkit-appearance: none;
      // -moz-appearance: none;
      width: 15px;
      height: 15px;
      border-radius: 50%;
      background-color: darkred;
      position: relative;

      &:active {
        box-shadow: 0 0 0 3px white, 0 0 0 6px darkred;
      }
    }
  }
  .range-value {
    left: 50%;
    padding-bottom: 20px;
    transform: translateX(-50%);
    position: absolute;
    top: -50px;
    // right: -80px;
    width: 40px;
    height: 40px;
    line-height: 40px;
    border-radius: 50%;
    background-color: #cee;
    text-align: center;
    font-size: 18px;
    font-weight: 800;
    text-align: center;
  }
}

/** button */
.btn {
  margin-top: 50px;
  width: 300px;
  height: 50px;
  /*outline: none;*/
  background-color: #999;
  /*border: 2px solid darken(#faeec5, 30%);*/
  border-color: transparent;
  border-radius: 5px;
  cursor: pointer;
  color: #eee;
  font-weight: bold;
  text-transform: uppercase;
  &:hover {
    border-color: #ddd;
    background-color: #eee;
    color: rgba(#000, 0.75);
  }
}
/**
 *  display
 */
.result {
  h2 {
    font-style: italic;
    font-weight: 500;
  }
}
.display {
  border-bottom: 1px solid gray;
  max-width: 400px;
  height: 70px;
  margin: 0 auto;
}
  @media only screen and (max-width: 650px) {
    .boxes {
      flex-direction: column;
      align-items: center;
      margin: 0 !important;
      /*justify-content: space-between;*/
      a {
        margin-top: 5px;
      }
      .box {
        min-width: 250px !important;
        min-height: 160px !important;
      }
    }


  }
</style>



