<template>
  <div class="menu">
    <router-link to="/">
      <img src="../assets/Home.png" @click="goToStart" alt="percent calculate" width="30">
    </router-link>
    <img src="../assets/information.png" alt="information" width="30">
    <!-- TODO запилить компонент инфо-->
    <span class="settings-btn" v-show="start">
      <img src="../assets/settings.png" alt="settings" @click="showSettings">
    </span>
  </div>
</template>

<script>
export default {
  computed: {
    start() {
      return this.$store.state.status;
    }
  },
  methods: {
    showSettings() {
      this.$store.commit("changeSettingsVisibility");
    },
    goToStart() {
      this.$store.commit("goToStart");
    }
  }
};
</script>
<style>
</style>





