import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);



export default new Vuex.Store({
  state: {
    settingsVisibility: false,
    termin: '',
    range: '',
    status: false
  },
  mutations: {
    changeSettingsVisibility(state) {
      state.settingsVisibility = !state.settingsVisibility;
    },
    changeTermin(state, playhz) {
      state.termin = playhz.termin;
      state.range = playhz.range;
    },
    changeRange(state, playhz) {

      state.range = playhz;
    },
    goToStart(state) {
      state.status = false;
    },
    showSettingsBtn(state) {
      state.status = true;
    }
  }
});



