<template>
  <div class="component-wrapper">
    <h1>Kapital berechnen</h1>
    <div class="input-text-wrapper">
      <div class="field text-input">
        <label class="input-title" for="satz">Prozentteil:</label>
        <input type="text" @focus="erseMsg" @input="setPercent" v-model="percent">
        <div class="tooltip" v-if="showPers">{{msg}}</div>
      </div>
      <div class="field text-input">
        <label class="input-title" for="prozentsatz">Prozentsatz:</label>
        <input type="text" @focus="erseMsg" @input="setSatz" v-model="satz">
        <div class="tooltip" v-if="showSatz">{{msg}}</div>
      </div>
      <div class="common-msg" v-if="showMsg">{{msg}}</div>
    </div>
    <app-settings v-show="showSettings"></app-settings>

    <button class="btn" @click="getResult">berechnen</button>
    <h2>Ergebnis(Kapital)</h2>
    <div class="expl">{{explanation}}</div>
    <div class="display">
      <div class="result">{{result}}</div>
    </div>
  </div>
</template>
<script>
import Mix from "../mixins/forms.js";
export default {
  data() {
    return {
      // satz: "",
      // percent: "",
      // msg: null,
      // flag: false,
      // min: "",
      // max: "",
      // range: 0,
      // termin: "",
      // pattern: /^[\d]{1,6}\.?[\d]{0,2}$/,
      // showPers: false,
      showSatz: false,
      // showKap: false,
      // showMsg: false,
      explanation: ""
      // result: ""
    };
  },
  created() {
    this.$store.commit("showSettingsBtn");
  },
  computed: {},
  mixins: [Mix],
  methods: {
    setPercent(e) {
      this.showSatz = false;
      this.percent = this.validate(this.percent).trim();
      if (this.flag) {
        this.flag = false;
        this.showPers = true;
      } else {
        this.showPers = false;
      }
    },

    setSatz(e) {
      this.showPers = false;
      this.satz = this.validate(this.satz).trim();
      console.log(1);

      if (this.flag) {
        this.flag = false;
        this.showSatz = true;
      } else {
        this.showSatz = false;
      }
    },
    erseMsg() {
      this.showMsg = false;
      this.showSatz = false;
      this.showKap = false;
    },
    setTime() {
      console.log(1);
    },

    /**
     *  переделать getResult для поиска капитала
     */
    getResult() {
      if (this.satz === "" || this.percent === "") {
        this.msg = "заполните все поля!";
        this.showMsg = true;
        return;
      }
      if (this.getTermin === "") {
        this.result = (parseFloat(this.percent) * 100) / parseFloat(this.satz);
        this.result = parseFloat(this.result.toFixed(2));
      }
      if (this.getTermin === "tage") {
        this.result =
          (parseInt(this.percent) * 36000) /
          (parseInt(this.satz) * parseInt(this.$store.state.range));
        this.result = parseFloat(this.result.toFixed(2));
      }
      if (this.getTermin === "monate") {
        this.result =
          (parseInt(this.percent) * 1200) /
          (parseInt(this.satz) * parseInt(this.$store.state.range));
        this.result = parseFloat(this.result.toFixed(2));
      }
      if (this.getTermin === "jahre") {
        // TODO не видит чекбокс (переделать на стор). Если стоит год, то неправильно считает. Нужно рэйндж передвинуть, чтобы начал правильно считать
        //  TODO Нужно обнулять значения во Vuex при смене компонента
        this.explanation =
          "такое количество денег нужно вложить, чтобы получить введенную сумму";

        let nenner = (1 + this.satz / 100) ** this.$store.state.range;
        console.log(nenner);

        this.result = (this.percent / nenner).toFixed(2);
      }
    }
  }
};
</script>
<style lang="scss" scoped>
h1,
h2 {
  text-align: center;
  margin-top: 20px;
  margin-bottom: 20px;
}
// input[type="text"] {
//   width: 40px;
//   border: 1px solid black;
//   outline: none;
//   padding: 5px;
// }
// .field {
//   display: flex;
//   justify-content: space-between;
//   width: 150px;
//   margin: 20px auto;
// }
.btn {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}
</style>




