import Vue from 'vue';
import VueRouter from 'vue-router';
import App from './App.vue';
import Start from './components/StartPage.vue';
import Percent from './components/Proz.vue';
import Percentage from './components/Percentage.vue';
import Capital from './components/Capital.vue';
import Settings from './components/Settings.vue';
import Checkbox from "./components/Checkbox.vue";
import Menu from "./components/Menu.vue";
import store from './store';


Vue.component('StartPage', Start);
Vue.component('FindPercent', Percent);
Vue.component('FindPercentage', Percentage);
Vue.component('FindCapital', Capital);
Vue.component('AppSettings', Settings);
Vue.component('AppMenu', Menu);
Vue.component('AppCheckbox', Checkbox);


Vue.use(VueRouter);
const routes = [{
    path: '/',
    component: Start
  },
  {
    path: '/zinsen',
    component: Percent
  },
  {
    path: '/percentage',
    component: Percentage
  },
  {
    path: '/capital',
    component: Capital
  },

];

const router = new VueRouter({
  // mode: "history", // чтобы убрать # из url/
  routes
});

new Vue({
  el: '#app',
  store,
  render: h => h(App),
  router
});



